<template>
  <v-container fluid>
     <v-layout row wrap>
    
          <v-flex xs12 sm4 v-for="category in race_categories" :key="category.id">
            <v-card class="mr-1">
              <v-toolbar :color="category.color">
                <v-toolbar-title>{{category.name}}</v-toolbar-title>
                <v-spacer></v-spacer>
                 
                 <h3 class="display-3"  v-if="category.start">
                   <vue-countdown v-on:time-expire="handleTimeExpire" :seconds="getSeconds(category)" :start="true"></vue-countdown> 
                 </h3>
                 <v-btn v-if="!category.start"  @click="startRace(category)" color="info"><v-icon>timer</v-icon> START!</v-btn>
              </v-toolbar>

              <v-card>
                    <v-card-title>
                      <div>
                         <v-tooltip top  class="ml-2" >
                           <span slot="activator"> <v-icon>timer</v-icon> 
                               <b v-if="category.start"> {{ category.start | formatDate }} </b> 
                               <b v-else> {{ category.gunstart }} </b>   
                            </span>
                           <span>Gunstart</span>
                         </v-tooltip> 

                        <v-tooltip top class="ml-2">
                           <span slot="activator"> <v-icon>timer_off</v-icon> 
                              <b v-if="category.end"> {{ category.end | formatDate }} ({{ category.cutoff }} HOURS )</b> 
                              <b v-else> {{ category.cutoff }} HOURS  </b>
                           </span>
                           <span>Cut Off Time</span>
                         </v-tooltip> 
                      </div>
                    </v-card-title>
              </v-card>
 
               <v-list>
               <v-list-tile  
                      v-for="(runner,rank) in filteredAndOrderedRunners(participants,category.name)" 
                      :key="runner.id" avatar>
                      <small class="gray--text"> {{runner.index}}. </small>                    
                      <v-list-tile-action class="text-xs-right">
                        <v-chip outline label color="runner.gender == 'female' ? 'pink' : 'blue-grey darken-4'"><b>{{runner.bib_number}}</b></v-chip>
                      </v-list-tile-action>
                    
                      <v-list-tile-content class="pl-1">
                        <v-list-tile-title v-text="`${runner.last_name}  ${runner.first_name}`"></v-list-tile-title>
                      </v-list-tile-content>

                       <v-list-tile-action class="text-xs-right" >
                        <v-btn v-if="!runner.checkin" small @click="checkin(runner.id)">Check-in</v-btn>
                        <v-btn v-if="runner.checkin && !category.start" small disabled>START</v-btn>
                        <div v-if="runner.checkin && category.start && (runner.total_time == '99:99:99' || !runner.total_time) && !runner.status">
                             <v-menu :close-on-content-click="false"  :nudge-width="200"  v-model="runner.action_card" offset-x >  
                             <v-btn color="success" slot="activator" @click="getTotalTime(runner,category)"><v-icon>timer_off</v-icon></v-btn>
                                <v-card>
                                    <v-list>
                                      <v-list-tile avatar>
                                        <v-list-tile-avatar>
                                           <v-chip outline label><b>{{runner.bib_number}}</b></v-chip>
                                        </v-list-tile-avatar>

                                        <v-list-tile-content>
                                          <v-list-tile-title>{{runner.last_name}}  {{runner.first_name}}</v-list-tile-title>
                                          <v-list-tile-sub-title>{{ category.name }} - {{ runner.gender}}</v-list-tile-sub-title>
                                        </v-list-tile-content>

                                         <v-list-tile-action>
                                           <v-list-tile-title> {{ total_time_duration }}</v-list-tile-title>
                                           <v-list-tile-sub-title><small>Finished Time</small></v-list-tile-sub-title>
                                        </v-list-tile-action>
                                      </v-list-tile>
                                    </v-list>

                                    <v-divider></v-divider>
                                    <v-list>
                                    <v-card-actions>
                                      <v-spacer></v-spacer>
                                       <v-btn flat small class="warning mr-2" @click="didNotFinished(runner)">DNF</v-btn>
                                       <!-- <v-btn flat small @click="runner.action_card = false"><v-icon>close</v-icon></v-btn> -->
                                       <v-btn class="success" @click="finishLine(runner)">FINISHED</v-btn>
                                    </v-card-actions>
                                  </v-list>
                                  </v-card>
                              </v-menu>
                        </div>

                         
                       <v-btn v-if="runner.status == 'DNF' && category.start" small disabled>{{runner.status}} / {{runner.total_time}}</v-btn>
                        <v-chip  v-if="runner.checkin && category.start && runner.total_time != '99:99:99' && runner.status != 'DNF' ">
                         <b>
                          <v-avatar :class="runner.gender == 'female' ? 'pink white--text' : 'blue-grey darken-4 white--text'">{{runner.category_rank}}</v-avatar>
                          {{runner.total_time}}
                         </b>
                        </v-chip>

                      </v-list-tile-action>

                </v-list-tile>
                </v-list>
            </v-card>
          </v-flex>


           <v-snackbar top right multiLine v-model="start_race_snackbar" dark> 
                 <p class="title"> Start <b> {{selected_race.name}} </b>  at <b> {{ moment(start_race_date).format("hh:mm:ss A") }} </b> ? </p>
                 <v-btn flat color="success" @click.native="setRaceTimestamp(selected_race,start_race_date)">Confirm</v-btn>
                 <v-btn flat @click.native="start_race_snackbar = false">Cancel</v-btn>
          </v-snackbar>

     </v-layout>

      <v-snackbar top right v-model="notification_snackbar" color="success white--text">
              {{ notification_snackbar_message }}
      </v-snackbar>

  </v-container>
</template>

<script>
  import { db } from '../main'
  import moment from 'moment'
  import VueCountdown from '@dmaksimovic/vue-countdown'
  import _ from 'lodash'

  export default {
    data () {
      return {
        participants: [],
        race_categories: [],
        start_race_snackbar:false,
        selected_race: '',
        start_race_date: '',
        notification_snackbar_message: '',
        notification_snackbar: false,
        total_time_duration:'',
      }
    },
    firestore () {
      return {
        participants: db.collection('participants').orderBy('last_name'),
        race_categories: db.collection('race_categories').orderBy('name','desc'),
      }
    },
    components: {
        'vue-countdown': VueCountdown
    },
    methods: {
       moment(){
         return moment()
       },
       showDetails(id){
           this.$router.push('/'+id)
       },
       editCategory(id)
       {
         this.$router.push('/category/'+id)
       },
       checkin(id) {
        // this.notification_snackbar = true
         db.collection('participants').doc(id).update({checkin:new Date()})      
       },
       startRace(category)
       { 
       
        this.start_race_snackbar = true
        this.selected_race = category
        this.start_race_date = new Date()

       }, 
      setRaceTimestamp(category,start) {

         var cutoff_time =  moment(start).add(category.cutoff, 'hours').format()
         db.collection('race_categories')
           .doc(category.id)
           .update({
              start: moment(start).format(),
              end: cutoff_time,
          })
         this.start_race_snackbar = false
      },
      getSeconds(category) {
          return this.calculateDuration(moment(new Date()).format(),category.end)
      },
      calculateDuration(now,then){
         var diff = moment.duration(moment(then).diff(moment(now)))
         return diff.asSeconds()
      },
      finishLine(participant) {
        db.collection('participants').doc(participant.id).update({total_time: this.total_time_duration,status:'Finished'})  
      },
      didNotFinished(participant){
        db.collection('participants').doc(participant.id).update({total_time: this.total_time_duration, status:'DNF'})  
      },
      getTotalTime(participant,category) {
         var duration  = this.calculateDuration(category.start,moment().format())
         this.total_time_duration =  moment().startOf('day').seconds(duration).format('H:mm:ss')
      },
      handleTimeExpire () {
            alert('Time is up!');
      },
      filteredAndOrderedRunners(participants,category) {
        let count = 1
        let overall_rank = 1
        let male_rank = 1
        let female_rank = 1
        var runners =  _.chain(participants)
                        .filter(function(p){
                          return p.race_category == category
                        })
                        .orderBy(['status', 'total_time', 'last_name'],['asc','asc','asc'])
                        .map(function(p) {

                          if( !p.status && p.gender == 'male') {
                             _.assign(p, {category_rank:male_rank++})
                          }else if( !p.status && p.gender == 'female') {
                             _.assign(p, {category_rank:female_rank++})
                          }

                          _.assign(p, {index:count++})
                          return p
                          
                        })
                        .value() 

        return runners
      }

    },
    filters: {
       formatDate(value) {
          if (value) {
            return moment(String(value)).format('hh:mm:ssA')
          }
       }
    }
   /* created () {
      
        db.collection('race_categories').orderBy('name','desc').get().then((querySnapshot) => {
           querySnapshot.forEach((doc) => {
              const data = doc.data()
                    data.participants = this.fetchRunners(data.name)
                    this.race_categories.push(data)
           })
      })

    }*/
    
  }
</script>


